import { useState, useEffect } from "react";
import { useParams, Link, useNavigate } from "react-router-dom";
import { Fade } from "react-awesome-reveal";
import "./News.css";
import NewsBase from "./NewsBase";

const OnePageNews = () => {
  let { id } = useParams();
  const [page, setPage] = useState({});
  const history = useNavigate();

  useEffect(() => {
    NewsBase.forEach((item) => {
      if (item.id === Number(id)) {
        setPage(item);
      }
    });
  }, [id]);

  return (
    <div className="one-page-news">
      <Fade cascade triggerOnce>
        <img src={page.imgUrl} alt={page.title} />
        <h1>{page.title}</h1>
        <p>{page.fullInfo}</p>
        <p className="text-end">{page.date}</p>
        <div className="text-end mb-5">
          <Link
            to={"/news"}
            className="effect2 effect-2"
            onClick={() => history(-1)}
          >
            Ortga qaytish
          </Link>
        </div>
      </Fade>
    </div>
  );
};

export default OnePageNews;
